import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CategoryService } from 'src/app/services/category.service';
import { ProductService } from "../../services/product.service";

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.css']
})
export class AddProductComponent implements OnInit {

  addProductForm:FormGroup;

  catRes;
  catData;
  catErr;
  imgPath;

  constructor(
    private fb: FormBuilder,
    private catSer:CategoryService,
    private productSer:ProductService
  ) { }

  ngOnInit(): void {
    this.validates()
    this.getCategory()
  }


  validates(){
    this.addProductForm=this.fb.group({
      'title':['',Validators.required],
      'price':['',Validators.required],
      'discount':['',Validators.required],
      'category':['',Validators.required],
      'description':['',Validators.required]
    })
  }

  pImage(e){
    if(e.target.files.length > 0){
      this.imgPath=e.target.files[0];
    }
    
    
  }

  addProduct(){
    let fData=this.addProductForm.getRawValue();
    console.log(fData);

    let formData=new FormData();

    formData.append('title',fData.title);
    formData.append('cname',fData.category);
    formData.append('username',localStorage.getItem('username'));
    formData.append('price',fData.price);
    formData.append('discount',fData.discount);
    formData.append('Image',this.imgPath);
    formData.append('description',fData.description);
    
    
    
    
    



    this.productSer.addPro(formData).subscribe(res=>{
      console.log(res);
      
    })
    
  }


  getCategory(){
    this.catSer.getCategory().subscribe(res=>{
      this.catRes=res;
      if(this.catRes.err==0){
        this.catData=this.catRes.msg;
        this.catErr=false;
      }else{
        this.catErr=true;
        alert('Cat Fetch Error Please Try Again Lates')
      }
      
    })
  }

}
