import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CategoryService } from "../../services/category.service";

@Component({
  selector: 'app-add-cat',
  templateUrl: './add-cat.component.html',
  styleUrls: ['./add-cat.component.css']
})
export class AddCatComponent implements OnInit {

  catForm:FormGroup;
  imgPath;
  resData;
  constructor(
    private fb:FormBuilder,
    private catSer:CategoryService,
    private router:Router
  ) { }

  ngOnInit(): void {
    this.validate()
  }

  

  getImg(e){
    if(e.target.files.length > 0){
      this.imgPath=e.target.files[0]
      // console.log(this.imgPath);
    }else{
      alert('Please Choose File')
    }
    
    
  }


  validate(){
    this.catForm=this.fb.group({
      'catName':['',Validators.required],
      'catDesc':['',Validators.required]
    })
  }

  addCat(){
    let fData=this.catForm.getRawValue()
    // console.log(fData);
    
    let formData=new FormData();
    
    formData.append('catName',fData.catName);
    formData.append('catDesc',fData.catDesc);
    formData.append('Image',this.imgPath);
    formData.append('username',localStorage.getItem('username'))

  
    

    this.catSer.addCategory(formData).subscribe(
      res=>{
        this.resData=res;
        if(this.resData.err==0){
          this.router.navigate(['dashboard/category'])
        }else{
          alert('Please Try Again Later')
        }
        
      }
    )
    
    
  }

}
