import { Component, OnInit } from '@angular/core';
import { CategoryService } from 'src/app/services/category.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {

  error=false;
  resData;
  catData;
  errData
  constructor(
    private catSer:CategoryService
  ) { }

  ngOnInit(): void {
    this.catSer.getCategory().subscribe(
      res =>{
        this.resData=res;
        if(this.resData.err==0){
          if(this.resData.msg.length > 0){
            this.catData=this.resData.msg;
          }else{
            this.error=true;
            this.errData='No Category Found'
          }
        }else{
          this.error=true;
          this.errData=this.resData.msg;
        }
        
      }
    )
  }


  delCat(id){
    this.catSer.deleteCat(id).subscribe(
      res =>{
        this.resData=res;

        if(this.resData.err==0){
          alert(this.resData.msg)
        }else{
          alert(this.resData.msg)
        }
        
      }
    )
  }

}
