import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CategoryService } from 'src/app/services/category.service';
import { UsersService } from 'src/app/services/users.service';

@Component({
  selector: 'app-cpass',
  templateUrl: './cpass.component.html',
  styleUrls: ['./cpass.component.css']
})
export class CpassComponent implements OnInit {

  passForm:FormGroup;
  resData;
  errCode;

  constructor(
    private fb:FormBuilder,
    private userSer:UsersService,
    private router:Router
  ) { }

  msg;
  ngOnInit(): void {
    this.validates()
  }

  validates(){
    this.passForm=this.fb.group({
      'opass':['',Validators.required],
      'npass':['',Validators.required],
      'cpass':['',Validators.required],
    })
  }

  changePass(){
    let formData=this.passForm.getRawValue();
    // console.log(formData);
    if(formData.npass!==formData.cpass){
      this.msg="Please Put Same New Pass And Confirm Pass"
    }else if(formData.opass===formData.npass){
      this.msg="old pass and new pass cant be same"
    }else{
      this.userSer.changePass({id:localStorage.getItem('username'),op:formData.opass,np:formData.npass}).subscribe(res=>{
        this.resData=res;
        this.errCode=this.resData.err;
        if(this.resData.err==0){
          localStorage.removeItem('username');
          this.msg=this.resData.msg;
          setTimeout(() => {
            this.router.navigate(['/'])
          }, 3000);
        }else{
          this.msg=this.resData.msg;
        }
        
      })
    }
    

  }

}
